ución, detrimento o menoscabo significativo a uno o más elementos del mismo, a consecuencia de la ejecución de una actividad o proyecto. ")])])])])])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-10"},[_c('div',{staticClass:"outlined-card py-5"},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"p-3"},[_c('h3',[_vm._v(" Medidas de control")]),_c('p',{staticClass:"mb-5"},[_vm._v("Estas se consideran una serie de medidas destinadas a enfrentar con éxito los riesgos identificados en cada una de las etapas de una actividad o proyecto toda vez que ocurra un riesgo específico, minimizando los daños a los trabajadores, a comunidades vecinas, al medio ambiente circundante y a las instalaciones. ")])])])])])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-10"},[_c('div',{staticClass:"outlined-card py-5"},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"p-3"},[_c('h3',[_vm._v("Medidas de mitigación")]),_c('p',{staticClass:"mb-5"},[_vm._v("Estas se entienden como la implementación o aplicación de cualquier política, estrategia, obra y/o acción tendiente a minimizar los impactos adversos que puedan presentarse en cierta actividad productiva o durante las etapas de ejecución de un proyecto y mejorar la calidad ambiental aprovechando las oportunidades existentes.")])])])])])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-10"},[_c('div',{staticClass:"outlined-card py-5"},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"p-3"},[_c('h3',[_vm._v("Medidas de compensación")]),_c('p',{staticClass:"mb-5"},[_vm._v("Son las actividades dirigidas a retribuir a las comunidades, las regiones, localidades y entorno natural, por los impactos o efectos negativos generados por un proyecto, que no